html {
  font: 100%;
  box-sizing: border-box;
  min-height: 100vh;
  min-width: 100vw;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

img {
  display: block;
  margin: 0;
}

a {
  text-decoration: none;
  display: block;
}

:root {
  --primary-text: hsla(21, 34%, 24%, 1);
  --btn-color: hsla(21, 45%, 29%, 1);
  --nav-bg: hsla(15, 35%, 32%, 0.8);
  --nav-active-bg: hsla(0, 0%, 100%, 1);
  --input-box: hsla(38, 57%, 97%, 1);
  --contact-btn: hsla(36, 97%, 60%, 1);
  --menu-bg: hsla(46, 100%, 94%, 1);
  --menu-main-text: hsla(190, 17%, 14%, 1);
  --menu-secondary-text: hsla(0, 0%, 33%, 1);
}

:root {
  --font-inika: "Inika", serif;
  --font-inter: "Inter", sans-serif;
  --font-outfit: "Outfit", sans-serif;
  --font-poppins: "Poppins", sans-serif;
  --font-recursive: "Recursive", sans-serif;
  --font-roboto: "Roboto", sans-serif;
}

body {
  background-image: url("../../../images/main_background.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 90rem;
  overflow-x: hidden;
}

.about {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-content: center;
}
.about__photo {
  transform: translateY(-7.8125rem);
  margin: 0 2.875rem 0 3.5625rem;
}
.about__hero {
  margin: 4.25rem 9.375rem 0 0;
  width: 35.625rem;
  height: 23.375rem;
}
.about__hero-title {
  width: 100%;
  height: 4.875rem;
  font-family: var(--font-inika);
  font-weight: 400;
  font-size: 3.625rem;
  line-height: 4.88625rem;
  color: var(--primary-text);
}
.about__hero-subtitle {
  width: 23.4375rem;
  height: 2.25rem;
  font-family: var(--font-poppins);
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 2.25rem;
  color: hsla(0, 40%, 7%, 0.75);
  margin-bottom: 1.5625rem;
}
.about__sub {
  width: 34.875rem;
  height: 11.375rem;
  font-family: var(--font-inter);
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.5125rem;
  color: hsla(0, 0%, 0%, 0.7);
}
.about__button {
  width: 10.625rem;
  height: 3.5rem;
  background-color: var(--btn-color);
  border: none;
  border-radius: 6.25rem;
  box-shadow: 0 0.3125rem 0.6875rem 0 hsla(0, 0%, 0%, 0.1);
  cursor: pointer;
  font-family: var(--font-poppins);
  font-weight: 600;
  font-size: 1rem;
  line-height: 1.2rem;
  color: var(--nav-active-bg);
}
.about__button:hover, .about__button:focus {
  transform: scale(1.1);
}

.blog {
  margin: 7.5625rem auto 6rem;
  width: 77.5625rem;
  height: 45.875rem;
}
.blog__subtitle {
  width: 10.625rem;
  height: 1.5rem;
  margin: 0 auto;
  font-family: var(--font-roboto);
  font-weight: 400;
  font-size: 1.375rem;
  line-height: 1.5rem;
  text-align: center;
  color: hsl(23, 39%, 33%);
}
.blog__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5.875rem;
  width: 100%;
  margin-top: 4.75rem;
}
.blog__item {
  width: 21.625rem;
  height: 34.4375rem;
  background-color: hsl(46, 100%, 94%);
  border-radius: 1.1875rem;
  padding: 2.4375rem;
  box-shadow: -0.125rem -0.5625rem 2.625rem 0 hsla(40, 27%, 24%, 0.38);
}
.blog__image {
  transform: translateX(0.4375rem);
  margin-bottom: 1.5rem;
}
.blog__date {
  width: 16rem;
  height: 1.4375rem;
  margin-bottom: 0.5rem;
  font-family: var(--font-outfit);
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 1.4175rem;
  color: hsl(22, 53%, 48%);
}
.blog__heading {
  width: 19.125rem;
  height: 4.125rem;
  margin-bottom: 0.75rem;
  font-family: var(--font-poppins);
  font-weight: 600;
  font-size: 1.375rem;
  line-height: 2.0625rem;
  color: hsl(23, 39%, 33%);
}
.blog__info {
  width: 16.1875rem;
  height: 4.125rem;
  margin-bottom: 1.875rem;
  font-family: var(--font-poppins);
  font-weight: 400;
  font-size: 0.78125rem;
  line-height: 1.171875rem;
  color: hsl(24, 37%, 32%);
}
.blog button {
  width: 5.76rem;
  height: 1.3825rem;
  background: transparent;
  border: none;
  cursor: pointer;
  font-family: var(--font-poppins);
  font-weight: 500;
  font-size: 0.9375rem;
  line-height: 1.40625rem;
  color: hsl(23, 39%, 33%);
  text-align: left;
}

.adjust {
  transform: translateX(1.25rem);
}

.contact {
  width: 76.9375rem;
  height: 40.98375rem;
  margin: 0 5.5625rem 4.3125rem 7.5rem;
}
.contact__title {
  width: fit-content;
  height: 5.1875rem;
  font-family: var(--font-inika);
  font-weight: 400;
  font-size: 4rem;
  line-height: 5.211875rem;
  color: var(--primary-text);
}
.contact__section {
  display: flex;
  flex-flow: row nowrap;
}
.contact__info {
  width: 50rem;
  height: 3.875rem;
  font-family: var(--font-roboto);
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.9375rem;
  color: var(--primary-text);
}
.contact__name-pass {
  display: block;
  width: 20.3125rem;
  height: 3.875rem;
  margin-bottom: 1.3125rem;
  padding-left: 1.5rem;
  border: none;
  border-radius: 3.75rem;
  box-shadow: 0 0.25rem 0.25rem 0 hsla(0, 0%, 0%, 0.26);
  font-family: var(--font-inter);
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.5125rem;
  text-align: left;
}
.contact__message {
  width: 20.3125rem;
  height: 10.0625rem;
  border: none;
  border-radius: 1.625rem;
  padding: 1.46125rem 0 0 1.506875rem;
  margin-bottom: 1.49875rem;
  font-family: var(--font-inter);
  font-weight: 400;
  font-size: 1.25rem;
  line-height: 1.5125rem;
}
.contact__submit {
  display: block;
  width: 27.75rem;
  height: 4.291875rem;
  background-color: hsl(36, 97%, 60%);
  border: none;
  border-radius: 3.75rem;
  box-shadow: 0 0.25rem 0.25rem 0 hsla(0, 0%, 0%, 0.25);
  cursor: pointer;
  font-family: var(--font-inter);
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 1.815625rem;
}
.contact__submit:hover, .contact__submit:focus {
  transform: scale(1.1);
}

.footer {
  width: 52.3125rem;
  height: 3.04625rem;
  margin: 0 30.1875rem 4.14125rem 7.5rem;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  gap: 2.835625rem;
}
.footer__socials {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  gap: 1.075625rem;
}
.footer__icons {
  background-color: white;
  width: 3.22625rem;
  height: 3.22625rem;
  border-radius: 50%;
  display: grid;
  place-content: center;
}
.footer__trademark {
  width: 33.0625rem;
  height: 1.6875rem;
  font-family: var(--font-inter);
  font-weight: 500;
  font-size: 1.375rem;
  line-height: 1.664375rem;
  color: var(--primary-text);
}
.footer__trademark span {
  color: white;
  font-weight: 400;
  margin-right: 0.469375rem;
}

.header {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  margin: 0.375rem 12.25rem 0 5.875rem;
}
.header nav {
  width: 27.875rem;
  height: 2.6875rem;
  background-color: var(--nav-bg);
  border-radius: 0.625rem;
  margin-left: 14.0625rem;
}
.header__menu-section {
  display: grid;
  width: 27.875rem;
  height: 2.6875rem;
  grid-template-columns: repeat(4, 1fr);
  place-content: center;
}
.header li {
  list-style-type: none;
  font-family: var(--font-inter);
  font-weight: 600;
  font-size: 1rem;
  line-height: 1.21rem;
  text-align: center;
  align-self: center;
}
.header li a {
  color: var(--nav-active-bg);
  display: grid;
  place-content: center;
}
.header li a:hover,
.header li a:focus {
  color: var(--nav-bg);
  background-color: var(--nav-active-bg);
  border-radius: 0.625rem;
  height: 2.6875rem;
  cursor: pointer;
}
.header__btn {
  width: 6.5rem;
  height: 3.0625rem;
  border: 0.125rem solid var(--primary-text);
  border-radius: 2.375rem;
  background: transparent;
  margin: 0 14.375rem 0 13.125rem;
  cursor: pointer;
  display: block;
}
.header__btn:hover, .header__btn:focus {
  transform: scale(1.1);
}

.homepage {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin: 0 0 0 7.875rem;
}
.homepage__hero-title {
  width: 54rem;
  height: 10.375rem;
  font-family: var(--font-inika);
  font-weight: 400;
  font-size: 4.3125rem;
  line-height: 5.175rem;
  color: var(--primary-text);
}
.homepage__sub {
  margin: 1.75rem 0 2.375rem 0;
  width: 26.6875rem;
  height: 3.625rem;
  font-family: var(--font-inter);
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 1.8rem;
  color: hsla(0, 42%, 7%, 0.75);
}
.homepage__buttons {
  display: grid;
  grid-template-columns: 9.1875rem 10.4375rem;
  height: 3.5rem;
  gap: 0.75rem;
}
.homepage__button-1 {
  background-color: var(--btn-color);
  border: none;
  border-radius: 6.25rem;
  cursor: pointer;
  font-family: var(--font-poppins);
  font-weight: 600;
  font-size: 0.9375rem;
  line-height: 1.125rem;
  color: var(--nav-active-bg);
}
.homepage__button-1:hover, .homepage__button-1:focus {
  transform: scale(1.1);
}
.homepage__button-2 {
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
  gap: 0.3125rem;
  background: transparent;
  border: 0.0625rem solid hsl(21, 42%, 17%);
  border-radius: 6.25rem;
  cursor: pointer;
}
.homepage__button-2:hover, .homepage__button-2:focus {
  transform: scale(1.1);
}
.homepage__button-2 div {
  font-family: var(--font-poppins);
  font-weight: 600;
  font-size: 0.9375rem;
  line-height: 1.125rem;
  color: var(--primary-text);
}
.homepage__photo {
  transform: translate(-10rem, -5.625rem);
}

.menu {
  width: 80.4375rem;
  height: 34.0625rem;
  margin: 0 2.0625rem 0 7.5rem;
}
.menu__title {
  width: fit-content;
  height: 5.1875rem;
  margin: 0 auto;
  font-family: var(--font-inika);
  font-weight: 400;
  font-size: 4rem;
  line-height: 5.211875rem;
  text-align: center;
  color: var(--primary-text);
}
.menu__section {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 4.0625rem;
  margin: 1.6875rem 0 3rem;
}
.menu__item {
  width: 22.1875rem;
  height: 21.9375rem;
  border-radius: 2.4375rem;
  background-color: var(--menu-bg);
  box-shadow: 0.125rem 0.5625rem 2.625rem 0 hsla(40, 27%, 24%, 0.5);
}
.menu__photo {
  display: grid;
  place-content: center;
  margin-top: 2.0625rem;
}
.menu__subtitle {
  width: 15.875rem;
  height: 2.0625rem;
  margin: 1.375rem 2.1875rem 0 1.125rem;
  font-family: var(--font-inika);
  font-weight: 700;
  font-size: 1.5625rem;
  line-height: 2.035625rem;
  color: var(--menu-main-text);
}
.menu__text {
  width: 17.875rem;
  height: 1rem;
  margin: 0 0 1.375rem 1.125rem;
  font-family: var(--font-recursive);
  font-weight: 400;
  font-size: 0.8125rem;
  line-height: 0.975rem;
  color: var(--menu-secondary-text);
}
.menu__bottom {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 2.1875rem 0 1.125rem;
}
.menu__bottom > div {
  font-family: var(--font-inter);
  font-weight: 700;
  font-size: 1.3125rem;
  line-height: 1.588125rem;
  color: hsl(21, 26%, 36%);
}
.menu__stars {
  display: flex;
  flex-flow: row nowrap;
}
.menu__button {
  display: flex;
  flex-flow: row nowrap;
  justify-self: flex-end;
  justify-content: space-between;
  align-items: center;
  width: 9.875rem;
  height: 1.8125rem;
  transform: translateX(72.5rem);
  background: transparent;
  border: none;
  cursor: pointer;
  color: hsl(46, 100%, 94%);
}
.menu__btn-txt {
  width: 7.625rem;
  text-align: left;
  font-family: var(--font-inter);
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 1.815625rem;
}

.material-symbols-outlined {
  font-variation-settings: "FILL" 0, "wght" 400, "GRAD" 0, "opsz" 24;
  width: 0.5625rem;
  height: 1.0625rem;
  transform: translate(-0.9375rem, -0.125rem);
}

.dollar {
  font-size: 1.125rem;
  line-height: 1.36125rem;
  color: hsl(98, 65%, 39%);
}